import React, { useEffect, useState } from "react";
import { MainPost } from "@/components/post/MainPost"; // 各ポストを表示するコンポーネント
import { PageTitle } from "@/components/PageTitle"; // タイトル表示コンポーネント
import { Post, posts } from "@/types/Post"; // 投稿の型とダミーデータ

export const PostPage: React.FC = () => {
  const [postPages, setPostPages] = useState<Post[]>([]);

  useEffect(() => {
    setPostPages(posts);
  }, []);

  return (
    <div className="mx-auto w-4/5">
      <PageTitle ttl="記事一覧" />
      {postPages.map((postPage) => (
        <MainPost key={postPage.id} post={postPage} />
      ))}
    </div>
  );
};
